<script>
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import { required } from '@/@core/utils/validations/validations'
import {
  BFormInput,
  BFormGroup,
  BForm,
  BRow,
  BCol,
  BFormSelect,
  BFormFile,
  BModal,
} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

const dictionary = {
  ar: {
    names: {
      name: 'اسم التصنيف',
      type: 'نوع التصنيف',
      image: 'صورة التصنيف',
    },
  },
}
localize('ar', dictionary.ar)
export default {
  name: 'CompanyDetails',
  components: {
    ValidationProvider,
    ValidationObserver,
    BRow,
    BCol,
    BFormSelect,
    BFormFile,
    BForm,
    BFormGroup,
    BFormInput,
    BModal,
    // eslint-disable-next-line vue/no-unused-components
    ToastificationContent,
  },
  props: {
    obj: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      dataId: '',
      dataName: '',
      dataType: '',
      dataImage: '',
      counts: {},
      dataColor: '',
      dataStatus: '',
      statuses: [
        {
          id: 1,
          name: 'أعمال حره',
        },
        {
          id: 2,
          name: 'شركة',
        },
        {
          id: 3,
          name: 'الكل',
        },
      ],
      typeOptions: [
        {
          id: 1,
          name: 'تطبيق',
        },
        {
          id: 1,
          name: 'لوحة تحكم',
        },
      ],
      countryOptions: [],
      categoryOption: [],
      category_file: null,
      required,
    }
  },
  watch: {
    obj() {
      this.fetchData()
    },
  },
  created() {
    this.$store.dispatch('common/FETCH_GOV').then(res => {
      this.countryOptions = res.data.data.governorates
    })
    this.$store.dispatch('categories/fetch').then(res => {
      this.categoryOption = res.data.data
    })
  },
  methods: {
    fetchData() {
      this.$store.dispatch('company/showDetails', this.obj.user.id).then(res => {
        this.counts = res.data.data
      })
    },
    update() {
      const Obj = new FormData()
      Obj.append('id', this.dataId)
      Obj.append('category_id', this.dataId)
      Obj.append('name', this.dataName)
      Obj.append('status', this.dataStatus)
      Obj.append('type', this.dataType)
      if (this.dataImage.type !== String) {
        Obj.append('image', this.dataImage)
      }
      Obj.append('color', this.dataColor)

      this.$store.dispatch('categories/edit', Obj).then(() => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: 'تم تعديل التصنيف بنجاح',
            icon: 'CheckCircleIcon',
            variant: 'success',
          },
        })
      })
    },
    fileCategory(event) {
      // eslint-disable-next-line prefer-destructuring
      this.dataImage = event.target.files[0]
    },
  },
}
</script>

